
@media only screen and (max-width: 1108px) {
    #main-box {
        flex-direction: column !important;
    }
}

#theme-box-border {
    height: fit-content;
    min-width: 50%;
}

#spinner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
