#form-collect {
    min-width: 33%;
    margin: 0 5px 10px 5px;
}

#theme-box-border {
    box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.3);
}

.file {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.file > input[type='file'] {
    display: none;
}

.file > label {
    font-size: 1rem;
    font-weight: 400;
    cursor: pointer;
    outline: 0;
    user-select: none;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fff;
    color: #333;
    padding: 12px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    transition: background-color 0.3s, border-color 0.3s;
}

.file > label:hover {
    border-color: #999;
}

.file > label:active {
    background-color: #f0f0f0;
}

.file > label > i {
    padding-right: 8px;
}

.file--upload > label {
    color: #007bff;
    border-color: #007bff;
}

.file--upload > label:hover {
    border-color: #0056b3;
    background-color: #e6f0ff;
}

.file--upload > label:active {
    background-color: #cce0ff;
}

.blah {
    height: 60px;
    border-radius: 6px;
    margin: 0 10px;
}
