.info-text {
    margin-bottom: 30px;
}

#theme2 {
    padding: 47px 60px;
}

#resume-avatar {
    width: 150px;
    height: 150px;
    object-fit: cover;
    object-position: center center;
}

/* Media Queries */
@media only screen and (max-width: 715px) {
    #theme2 {
        padding: 30px 20px;
    }
   
    #resume-avatar {
        width: 120px;
        height: 120px;
    }

    .info-text {
        width: 280px;
    }

    .summary-text {
        width: 100% !important;
    }
}

@media only screen and (max-width: 430px) {
    #theme2 {
        padding: 20px 15px;
    }
}
