.info-text {
    margin-bottom: 30px;
}

#theme3 {
    padding: 47px 60px;
}

/* Media Queries */
@media only screen and (max-width: 715px) {
    #theme3 {
        padding: 30px 20px;
    }

    .info-text {
        width: 280px;
    }

    .summary-text {
        width: 100% !important;
    }
}

@media only screen and (max-width: 430px) {
    #theme3 {
        padding: 20px 15px;
    }
}
