/* Skills Set */
.skill-badge {
  background-color: #e0f2f1;
  color: #333;
  margin: 5px;
  padding: 8px 16px;
  border-radius: 5px;
}

.skillBox {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.basic-set {
  margin: 0 2rem;
  padding-top: 10px;
}

/* Utility */
.subBox {
  margin-bottom: 15px;
}

.sub-details {
  padding: 0 12px;
}

.sub-title {
  font-size: 1.2rem;
  font-weight: 600;
  color: #333;
}

/* Media Queries */
@media only screen and (max-width: 450px) {
  .basic-set {
    margin: 0 1rem;
  }
}
