.Bullet_Points {
    display: flex;
    margin: 15px 0;
    align-items: center;
}

.Bullet_Points p {
    margin: 0 10px;
    color: #555;
}

.templatesList {
    font-family: "Poppins", sans-serif;
    color: #333;
    text-align: center;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    background-color: #e9ecef;
    padding: 30px;
}

.template {
    font-family: "Poppins", sans-serif;
    color: #333;
    text-align: center;
    height: 300px;
    width: 250px;
    overflow: hidden;
    border-radius: 10px;
    margin: 15px;
    border: 1px solid #ced4da;
    background-color: #ffffff;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.template:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);
}

.template img {
    max-width: 100%;
    min-height: 100%;
    max-height: 100%;
    object-fit: cover;
    border-bottom: 1px solid #ced4da;
}
